import { rateListActions } from '../actions/rateList.actions';

const initState = {
    data: null,
    error: null,
    loading: false,
};

export const rateList = (state = initState, { type, payload }) => {
    switch (type) {
        case rateListActions.trigger:
            return {
                ...initState,
                loading: true,
            };
        case rateListActions.success:
            return {
                ...initState,
                data: payload,
            };
        case rateListActions.failure:
            return {
                ...initState,
                error: payload,
            };
        default:
            return state;
    }
};
